.movie-result-container {
    width: 100%;
    color: #000;
    border-bottom: 1px solid #fff;
    padding: 4px;
    display: -ms-flexbox;
    display: flex;
}

.movie-result-container:hover {
    -webkit-transition: background-color .1s ease-in-out;
    -o-transition: background-color .1s ease-in-out;
    transition: background-color .1s ease-in-out;
    background-color: #b8c3cc;
}

.movie-result-container img {
    width: 50px;
    height: 50px;
    margin-right: 5px;
}

.movie-result-title {
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    max-width: 100px;
    font-size: 15px;
    white-space: nowrap;
    margin-top: 15px;
}

.movie-result-container a {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    color: #fff;
}


